@import '../../styles/breakpoints';

// SecondaryNav Variables:
// --sb-secondary-nav-background - background color
// --sb-secondary-nav-text-color - text color
// --sb-secondary-nav-height - height
:root {
  --sb-secondary-nav-height: 50px;
}

.sb-secondary-nav {
  align-items: center;
  background-color: var(
    --sb-secondary-nav-background,
    var(--sbt-color-background)
  );
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.1), 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  color: var(--sb-secondary-nav-text-color);
  display: flex;
  height: var(--sb-secondary-nav-height);
  padding: 0.5rem 1rem;
  width: 100%;

  @include window-width-max-small {
    padding: 0.5rem;
  }
}

.sb-secondary-nav__title-region {
  margin-right: 32px;
  padding-left: 0.5rem; // to match navbar
}

.sb-secondary-nav__nav-region {
  .sb-button {
    margin-right: 12px;
  }
}
